import React from 'react';

import './ModifyYourList.css';
import TrackList from '../TrackList/TrackList';
import Playlists from './Playlists'

class ModifyYourList extends React.Component{
  constructor(props){
  super(props);
  this.state={
    term: ''
};
this.handlePlaylistName=this.handlePlaylistName.bind(this);
  this.onAddItems=this.onAddItems.bind(this);
  this.addName=this.addName.bind(this);

  }


// componentDidMount(){
// this.props.onCheckYourLists()
  


handlePlaylistName(event){
  this.setState({term: event.target.value});
  this.props.onNameChange(event.target.value);
}



onAddItems(){
  
  this.props.onAddItems();
  this.setState({term:''})
}
addName(name){
  this.setState({term: name});
  // the problem was that when setting state throug this function, the value of input was set just fine
  // but onChange wasnt triggered and handlePlaylistName wasnt setting the playlistName in Appjs - therfore
  // Spotyfi.addItems was getteing wrong name for POST request, yet the line below sorts that issue:
this.props.onNameChange(name);
}

render(){
        
  return (
      <div className="Playlists">
    <div className="AddItems-topbar">    
<input className ="Playlists-input" onChange={this.handlePlaylistName} placeholder="playlist's name" value= {this.state.term}  />
<button className="Playlists-save" onClick={this.onAddItems} >Add tracks</button>
<TrackList tracks={this.props.playlistTracks}
 onRemove= {this.props.onRemove}
 isRemoval={true}/>
</div>
<div className="AddItems-playlists">
 {/* <TrackList tracks={this.props.playlistTracks}
 onRemove= {this.props.onRemove}
 isRemoval={true}/>  */}
 {/* <button className="Playlists-save" onClick={this.onAddItems}>Modify your list</button> */}

  
        {/* <h2>Your current lists:</h2> */}
        <button onClick={this.props.onCheckYourLists} className="Playlists-save">Check your current lists:</button>
   
      <div>{this.props.onYourSpotifyLists.map(item=>{
return <Playlists key={item.id} onAddName={this.addName}
name={item.name} id={item.id}/>})}
</div>
</div>
 </div>);

  }
}

export default ModifyYourList;