import React from 'react';

import './Playlist.css';
import TrackList from '../TrackList/TrackList';

class Playlist extends React.Component{
  constructor(props){
  super(props);
  this.state={
    term: ''
};
this.handlePlaylistName=this.handlePlaylistName.bind(this);
  this.savePlaylist = this.savePlaylist.bind(this);}
  
  
  
  // ist =  this.props.onyourSpotifyLists.map(list=>
//     <li>{list}</li>
//   )
//   }


handlePlaylistName(event){
  this.setState({term: event.target.value});
  this.props.onNameChange(event.target.value);
}


savePlaylist(){
    this.props.onSave();
    this.setState({term:''})
}

    
    render(){
      // console.log('checkin props', this.props.playlistName)
        return (
            <div className="Playlist">
              {/* defaultvalue insted of value below was preventing list name 
              from refreshing after button was clicked, so weird */}
            {/* <input value={this.props.playlistName} onChange={this.handleNameChange}/> */}
            <input onChange={this.handlePlaylistName} placeholder="name your list" value= {this.state.term}  />
       
            <TrackList tracks={this.props.playlistTracks}
            onRemove= {this.props.onRemove}
            isRemoval={true}/> 
           
            <button className="Playlist-save" onClick={this.savePlaylist}>Save your playlist</button>
           
            
          </div>);
    
}
}

export default Playlist;