import React from 'react';
import Spotify from '../../util/Spotify';
import './Playlists.css';


class Playlists extends React.Component{
    constructor(props){
    super(props);
    this.state={
      tracks: []
  };
  
    this.getPlaylist=this.getPlaylist.bind(this);
    this.clearTracks=this.clearTracks.bind(this);
    this.addName=this.addName.bind(this);
    }
  
  
 
  
  
  
  
    getPlaylist(){
        
        Spotify.getPlaylist(this.props.id).then(reply=>  {
         this.setState({tracks: reply});
            console.log(reply)
        }
        
      
        )
      }


    clearTracks(){
        this.setState({tracks: []})
    }
    addName(){
      this.props.onAddName(this.props.name)
    }
      
      render(){
        
          return (<div >
            
            <ul>
              <li><h3 className="Playlists-header">
               {this.props.name}
            </h3> </li>
            </ul>
            <div className="Buttons">
            <button className="Playlists-buttons" onClick={this.getPlaylist}>see <br/>more</button>
            <button className="Playlists-buttons" onClick={this.clearTracks}>see<br/> less</button>
            <a href="#section2" className="Playlists-buttons"  onClick={this.addName}>add tracks</a>
            {/* <a href="#section2">Comming Soon</a> */}
            </div>
            {this.state.tracks.map(track=>{
            return <h5>{track.song}</h5>})}
      </div>)
          
  

        }}
  
  export default Playlists;