import React from 'react';

import './App.css';
import SearchBar from '../SearchBar/SearchBar';
import SearchResults from '../SearchResults/SearchResults';
 import Playlist from '../Playlist/Playlist';
import Topbar from '../Navigation/Topbar/Topbar';

import Spotify from '../../util/Spotify';
import ModifyYourList from '../ModifyYourList/ModifyYourList';

class App extends React.Component{
  localStorage;
  constructor(props){
    super(props);
    this.state ={
      searchResults:[],
      playlistName: 'New/Existing List',
      playlistTracks: [],
      yourSpotifyLists: [],
      users: "",
      auth: false,
      
      

    };
    this.getSearchResultsFromLocalStorage=this.getSearchResultsFromLocalStorage.bind(this);
    this.setSearchResultsInLocalStorage=this.setSearchResultsInLocalStorage.bind(this);
    this.setUsersNameInLocalStorage=this.setUsersNameInLocalStorage.bind(this);
    this.getUserFromLocalStorage=this.getUserFromLocalStorage.bind(this);
    this.logIn=this.logIn.bind(this);
    this.addTrack = this.addTrack.bind(this);
    this.removeTrack = this.removeTrack.bind(this);
    this.updatePlaylistName = this.updatePlaylistName.bind(this);
    this.savePlaylist =this.savePlaylist.bind(this);
    this.search = this.search.bind(this);
    this.addItemsToPlaylist= this.addItemsToPlaylist.bind(this);
    this.checkYourLists = this.checkYourLists.bind(this);
    this.logOut=this.logOut.bind(this);
    this.setAuthInLocalStorage=this.setAuthInLocalStorage.bind(this);
    this.getAuthromLocalStorage=this.getAuthromLocalStorage.bind(this);
    this.scrollToTop=this.scrollToTop.bind(this);
    
   
    
  }
// have to ask myself what do i want to out of those sorage saving functions,
// and that is only !one thing to get the auth and users after page refresh!, 
// that's it , when loging out everthing gets cleared including local sotrge values since
// Componet Did Mount runs after logOut() function and sets local storage to 
// what stare is at that time and that is default empty string

  componentDidMount(){
  
    // let local = JSON.parse(localStorage.getItem("userName"));
    //       if(local==="second"){
    //        this.logIn()
    //      }else{

          console.log('componentDidMount');
    this.getUserFromLocalStorage();
    this.getAuthromLocalStorage();
    this.getSearchResultsFromLocalStorage();
         
    
        }

      
      
        getAuthromLocalStorage(){
          if (localStorage.getItem("auth")===null) {
            localStorage.setItem("auth", JSON.stringify(this.state.auth));
          }else{
            let todoLocal = JSON.parse(localStorage.getItem("auth"));
            this.setState({auth:todoLocal});
          }}
      
      
       //  saving usersName to localStorage
        getUserFromLocalStorage(){if (localStorage.getItem("usersName")===null) {
          localStorage.setItem("usersName", JSON.stringify(this.state.users));
          }else{
          let user = JSON.parse(localStorage.getItem("usersName"));
          this.setState({users:user});
           }
        } 


   componentDidUpdate(prevState){
       console.log('componentDidUpdate')
      this.setSearchResultsInLocalStorage();
      this.setUsersNameInLocalStorage();
      this.setAuthInLocalStorage();
      }

  //     // ideal spot for fetchin data and steeing state would be in componentDidMout but
  //     // in this case that triggers a redirect and login to spotify so it basicallu looks that you open 
  //     // ur up and two mili-seconds later get a log in window to spotify, 
  //     // for that reason I will fetch my lists throug button click 
  //     // if(prevState.yourSpotifyLists!==this.state.yourSpotifyLists){
  //     // this.checkYourLists();}
    
      
  
    
        setAuthInLocalStorage(){
          localStorage.setItem("auth", JSON.stringify(this.state.auth));
          }; 
        setSearchResultsInLocalStorage(){
            localStorage.setItem("searchResults", JSON.stringify(this.state.searchResults));
            };
        setUsersNameInLocalStorage(){
              localStorage.setItem("usersName", JSON.stringify(this.state.users)
              )}
  
  
  
    
  
// saving sarch results in local storage
  
  getSearchResultsFromLocalStorage(){
      if (localStorage.getItem("searchResults")===null) {
        localStorage.setItem("searchResults", JSON.stringify([]));
      }else{
        let todoLocal = JSON.parse(localStorage.getItem("searchResults"));
        this.setState({searchResults:todoLocal});
      }}
  
  //     // ---

  
  
       
    
  

  
   logIn(){
      
    Spotify.getUsersName().then(usersName=>{

    if (usersName.name.length<1){
      
       this.setState({
        // users: usersName.name
        users: usersName.name
        });}
        else{
          this.setState({
            users: "Have fun!"
          })
        }
        this.setState({
          auth: true
        })
      }).catch(reject=>{
        this.setState({
          users: "Incognito"
        });
        this.setState({
          auth: false
        })
      });
      // .catch(onReject) is syntactic sugar for .then(undefined, onReject)
    
    }

     
      

      // clear local storage, clear window.location


      
    logOut(){
    
      const url = 'https://www.spotify.com/logout/';                                                                                                                                                                                                                                                                               
      const spotifyLogoutWindow = window.open(url, 'Spotify Logout', 'width=700,height=500,top=40,left=40')                                                                                                
      setTimeout(() => spotifyLogoutWindow.close(), 2000);
      this.setState({
      users:""
    });
    this.setState({
      searchResults: []
    });
    this.setState({
      auth: false
    });
    this.setState({yourSpotifyLists: []});
    localStorage.clear();  

    // access token was still sometimes satying in the app in those 
    // times when logut/login was pressed and app wasnot refreshed, then
    // the username was loading even though localsorag and state were cleared
    // yet accesstoken in Spotify module was equal to acces token so even 
    // without having to log into spotify api getUername function was working!
    // and loading userName
    Spotify.logOut(); 

  }
  
     
   
addTrack(track){
    let tracks = this.state.playlistTracks;
    if(tracks.find(savedTrack => savedTrack.id ===track.id)){
      return;
    }
    tracks.push(track);
    this.setState({playlistTrack: tracks});
    

  }

  removeTrack(track){
    let tracks = this.state.playlistTracks;
    tracks = tracks.filter(currentTrack => currentTrack.id !== track.id);
    this.setState({playlistTracks: tracks});
  }

  updatePlaylistName(name){
    this.setState({playlistName: name});
  }

  savePlaylist(){
    
    //alert("this method works")
    const trackUris=this.state.playlistTracks.map(track=>track.uri);
        Spotify.savePlayList(this.state.playlistName, trackUris).then (() => {
      this.setState({
        playlistName: 'New/Existing List', 
        playlistTracks: [],
        searchResults: []
      }); 
      this.checkYourLists();
    })
  }

addItemsToPlaylist(){
  const trackUris=this.state.playlistTracks.map(track=>track.uri);
  console.log('trackUris', trackUris);
    Spotify.addItemsToPlayList(this.state.playlistName, trackUris).then (() => {
      this.setState({
        playlistName: 'Name your playlist', 
        playlistTracks: []
      });
     
      
    });
    this.setState({
      playlistName: 'Name your playlist', 
      playlistTracks: []
    });
}

checkYourLists(){
  
  this.logIn();
  Spotify.checkYourLists().then(yourSpotifyLists => {
    this.setState({yourSpotifyLists: yourSpotifyLists});
    this.setState({auth: true})
  })
  
}

  search(term){ 
    // afetr second click serach results dissapear because there is nothing
    // in the input field
    
    // alert("this method is linked to the button correctly")
  Spotify.search(term).then(searchResults => {
    this.setState({searchResults: searchResults});
    this.setState({auth:true})
  });}

scrollToTop(){
  window.scrollTo({
    top: 0,
    left: 0,
    behavior: 'smooth'
  })
}
  

  render(){
    return(
    
      <div className="Main">
            {/* {!this.state.auth? <div className="No-auth"></div>: null} */}
        <Topbar onAuth={this.state.auth} logOut={this.logOut} logIn={this.logIn} />
  <h1>Manage<span className="highlight"> Your Spotify{this.state.users==="Have fun!"?<span>.</span>:null} </span>{this.state.users}</h1>
  {!this.state.auth? <h4>Send to tomspatula@gmail.com your Spotify email addresss and username for this app's usage, thanks </h4>: null}
        <butoon onClick={this.scrollToTop} id="btnScrollToTop">
          <i class="material-icons" >arrow_upward</i></butoon>
      <div className="App">
      <section id="section1" class="one">
                <h2 className="header">Create Your New Playlist</h2>
      <SearchBar onSearch ={this.search}/>
    <div className="App-playlist">
   
      <SearchResults searchResults={this.state.searchResults} 
      onAdd = {this.addTrack} />       
       <Playlist playlistName = {this.state.playlistName}  
       playlistTracks={this.state.playlistTracks}
       onRemove={this.removeTrack}
       onNameChange={this.updatePlaylistName}
       onSave= {this.savePlaylist} /> 
           </div>
  </section>
  <section id="section2" class="two">

    <h2 className="header">Add Tracks to Your Playlists</h2>
  <SearchBar onSearch ={this.search}/>
   
  <div className="App-playlist">     
  <SearchResults searchResults={this.state.searchResults} 
      onAdd = {this.addTrack} /> 
       <ModifyYourList playlistName = {this.state.playlistName}  
       playlistTracks={this.state.playlistTracks}
       onRemove={this.removeTrack}
       onNameChange={this.updatePlaylistName}
      
       onAddItems = {this.addItemsToPlaylist}
       onYourSpotifyLists={this.state.yourSpotifyLists}
       onCheckYourLists = {this.checkYourLists} />
    
        
           </div>
  </section>
  
  
  </div>
  </div>
  
    )
  }
}

export default App;
