import React from 'react';
import NavigationItem from '../NavigationItem/NavigationItem';

import classes from './Topbar.module.css';
// import Track from '../Track/Track';

class Topbar extends React.Component{
    // i dont think we need here constructor at all
    constructor(props){
        super(props);
    }
        


       render(){
      return (
            <div>
                <header>
                    <nav>
                        <NavigationItem auth={this.props.onAuth} logOut={this.props.logOut} logIn={this.props.logIn} users={this.props.usersName} />
                    </nav>
                </header>
               
                   
                
            
        </div>);
    
}
}

export default Topbar;