// this method is imported straigt into Playlists.js 
getPlaylist(id){
const accessToken = Spotify.getAccess();
    const headers = { 
        Authorization: `Bearer ${accessToken}`
    };
    let userId;
    return fetch('https://api.spotify.com/v1/me',
    {headers: headers}
    ).then(response => response.json()
    ).then(jsonResponse => {
                       userId = jsonResponse.id;
        return fetch(`https://api.spotify.com/v1/users/${userId}/playlists/${id}`,{
          headers: headers,
          
          
    }).then(response => response.json()
    )
    .then(jsonResponse=>{
        const listOfSongs =jsonResponse.tracks.items
        const titelsSongs= listOfSongs.map( item=>
       ( {
         song: item.track.name,
        }));
        return titelsSongs;

  })            

    
} )


},







