import React from 'react';

import './Track.css';

class Track extends React.Component{
    constructor(props){
         super(props);
         this.addTrack =this.addTrack.bind(this);
         this.removeTrack = this.removeTrack.bind(this); 


    }
    addTrack(){
        this.props.onAdd(this.props.track);

    }
    removeTrack(){
        this.props.onRemove(this.props.track);
    }


    renderAction(){
        if(this.props.isRemoval){
            console.log('checkin state',this.props.track)
            return (<div className="Track" >
            <div className="Track-information">
            
              <h4> {this.props.track.name}</h4>
              <p> {this.props.track.artists}</p>
            </div><button className="Track-action" onClick={this.removeTrack} ><span class="fa fa-trash-alt"/></button></div>)
            }else{
                return (<div className="Track" onClick={this.addTrack}>
                <div className="Track-information">
                  <h4> {this.props.track.name}</h4>
                  <p> {this.props.track.artists}</p>
                </div><button className="Track-action"><span class="fa fa-plus-square"/></button></div>)
            }
        }
        
    render(){
        return this.renderAction();         
        
}
}


export default Track;    