const NavigationItem = ( props ) => {
  
  const[active, setActive] = useState(false);

  const toggleClass= ()=>{
      
      setActive(!active)
  }
  
  return(
  <div class={active? "responsive topnav": "topnav"}  id="myTopnav">

  {/* <a class="welcome-section"> Welcome {props.users}</a> */}
  <a  href="#" class="icon" onClick={toggleClass}>
    <i class="fa fa-bars"></i>
  </a>

  <a href ="#">Home</a>
  <a href="#section1">Create</a>
  <a href="#section2">Add</a>
  <a href="#section3">Comming Soon</a>
  {!props.auth? <a href="#" class="button" onClick={props.logIn} >  Log in  </a> :
   <a href="#" class="button" onClick={props.logOut} > Log out </a> }
  

  </div>
)}
export default NavigationItem;